* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    overflow: hidden;
    font-family: Arial, Helvetica, sans-serif;
    background: rgb(50, 50, 50);
}

.webgl {
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}

natehub-modal {
    position: absolute;
    display: flex;
    justify-content: center;
    width: 100%;
    bottom: 0;
    left: 0;
    pointer-events: none;
}
@media (min-width: 600px) {
    natehub-modal {
        bottom: 40px;
    }
}

.modal__content {
    background: rgba(0, 119, 182, 0.75);
    border-top: 1px solid #0096c7;
    box-shadow: 1px 3px 76px 13px rgba(0,0,0,0.46);
    color: white;
    font-size: 14px;
    padding: 40px 20px;
    line-height: 1.5;
    position: relative;
    width: 100%;
    pointer-events: auto;
}

@media (min-width: 600px) {
    .modal__content {
        width: 600px;
    }
}

natehub-modal a,
natehub-modal a:visited {
    color: white;
}

.d-none {
    display: none;
}

natehub-modal h2 {
    margin-bottom: 0.8em;
}

natehub-modal p {
    margin-bottom: 1em;
}

natehub-modal img {
    max-height: 330px;
    max-width: 100%;
    object-fit: contain;
    margin-bottom: 1rem;
}

.modal__close {
    background: none;
    appearance: none;
    border: none;
    box-shadow: none;
    color: white;
    cursor: pointer;
    position: absolute;
    top: 5px;
    right: 10px;
    font-size: 24px;
    padding: 3px;
}

.loading {
    background: #101010;
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 1;
    transition: opacity 1s ease-in-out;
    transition-delay: 300ms;
}

.loading__message {
    width: 100%;
    color: #ccc;
    margin: 1em 0;
    transition: all 0.3s ease-in-out;
}

.loading__progress {
    width: 70%;
}

@media (min-width: 767px) {
    .loading__progress {
        width: 50%;
    }
}

.loading__progress-bar {
    background: #ccc;
    width: 100%;
    height: 10px;
    transform-origin: left;
    transform: scaleX(0);
    transition: transform 1s ease-in-out;
}

.loading.fade {
    opacity: 0;
}

.loading.fade .loading__message {
    opacity: 0;
    transform: translate(20px);
}

.loading.fade .loading__progress-bar {
    transform-origin: right;
    transform: scaleX(0);
}

.loading.complete {
    display: none;
}